.line {
  width: 100%;
  max-width: 1400px;
  height: 2px;
  margin: 15px 20px 15px 0px;
  position: relative;
  display: inline-block;
  background-color: rgba(255, 255, 255, 1);
}

.panel {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  margin: auto;
  display: flex;
  justify-content: center;
  position: relative;
  box-sizing: border-box;
  padding: 0 10vw;
  padding-bottom: 10%;
}

.container {
  display: flex;
  min-height: 20vh;
  margin-top: 130px;
  margin-bottom: 100px;
  flex-wrap: wrap;
  border: 16px solid #fff;
  max-width: 1447px+26px;
  position: relative;
  // max-width: 1310px;
  // background: yellow;
}

.containerGallery {
  width: 80%;
  display: flex;
  min-height: 1200px;
  margin-top: 80px;
  margin-bottom: 100px;
  flex-wrap: wrap;
  // border: 16px solid #fff;
  // min-width: 1520px;
  // padding: 0 15px;
  position: relative;
  // margin: 0 auto;
  max-width: 1520px;
  // background: yellow;
}

.panelHero {
  width: 100%;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;

  color: white;
}

.hero {
  // height: 90vh;

  // background: url('../img/sky.jpg') no-repeat bottom center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10vw;
  padding-bottom: 100px;
  color: white;
  animation: color-change-5x 35s linear 2s infinite alternate both;
}

.colorChange {
  // height: 90vh;

  // background: url('../img/sky.jpg') no-repeat bottom center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10vw;
  color: white;
  animation: color-change-5xx 55s linear 2s infinite alternate both;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  // margin-top: -15px;
  // margin-bottom: -15px;
  position: relative;
  // background-color: gray;
}

.padd-15 {
  padding-left: 15px;
  padding-right: 15px;
}

#logo {
  text-align: center;
}

.black {
  background-color: #040c26;
}
.gray {
  background-color: #777;
}
.blue {
  background-color: #2c7ad2;
}
.orange {
  background-color: #e77614;
}

.red {
  background-color: #e43f5a;
}

.purple {
  background-color: #c82736;
}
.green {
  background-color: #28a92b;
}

@media (max-width: 1200px) {
  .panel {
    padding: 0 10vw;
  }
  .row {
    // background-color: green;
    transition: all 0.3s ease-in-out;
  }

  .containerGallery {
    width: 100%;
    display: flex;
    min-height: 20vh;
    min-height: 20vh;
    margin-top: 120px;
    margin-bottom: 100px;
    flex-wrap: wrap;
  }
}

@media (max-width: 767px) {
  .panel {
    padding: 0 5vw;
  }
  .row {
    // background-color: red;
    transition: all 0.3s ease-in-out;
  }
  .container {
    // max-width: 500px;
    margin-top: 40%;
    border: 8px solid #fff;
    h1 {
      padding-bottom: 50px;
    }
  }

  .hero {
    padding: 0 2vw;
    // padding-bottom: 100px;
  }

  .panel {
    padding: 0 2vw;
  }
  .containerGallery {
    width: 100%;
    display: flex;
    min-height: 20vh;
    min-height: 20vh;
    margin-top: 80px;
    margin-bottom: 100px;
    flex-wrap: wrap;
  }
}
