.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  // margin-top: -15px;
  // margin-bottom: -15px;
  position: relative;
  // background-color: gray;
}

.padd-15 {
  padding-left: 15px;
  padding-right: 15px;
}

.portfolio .portfolio-filter {
  flex: 0 0 100%;
  max-width: 100%;
  text-align: left;
  margin-bottom: 40px;
}

.portfolio .portfolio-filter button {
  border: 2px solid white;
  background-color: transparent;
  font-size: 1.3rem;
  font-weight: 600;
  text-transform: capitalize;
  margin: 1rem 0.5rem 3rem 0;
  padding: 0.3rem 1.2rem !important;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  white-space: nowrap;
  padding-bottom: 2px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.portfolio .portfolio-filter button.active {
  background: #c82736;
}
.portfolio .portfolio-filter button:hover {
  background-color: white;
  color: black;
}
.portfolio .portfolio-filter button:focus {
  outline: none;
  background-color: white;
  color: black;
}
.portfolio .portfolio-item {
  /* background-color:  red; */
  flex: 0 0 33.33%;
  max-width: 33.33%;
  margin-bottom: 30px;
}
.portfolio .portfolio-item.hide {
  display: none;
}
.portfolio .portfolio-item.show {
  display: block;
  animation: showItem 0.5s ease;
}
@-webkit-keyframes showItem {
  0% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes showItem {
  0% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}

.shadow-dark {
  -webkit-box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);
  box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);
}

.portfolio .portfolio-item-inner {
  border: 4px solid #fdf9ff;
  // border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
}
.portfolio .portfolio-item-inner .portfolio-img img {
  width: 100%;
  display: block;
  transition: all 0.5s ease-in-out;
}

.portfolio .portfolio-item-inner:hover .portfolio-img img {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.portfolio .portfolio-item .portfolio-info {
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(4, 12, 38, 0);
  height: 100%;
  width: 100%;
  z-index: 1;
  padding: 30px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  opacity: 0;
}
.portfolio .portfolio-item-inner:hover .portfolio-info {
  opacity: 1;
}
.portfolio .portfolio-item .portfolio-info h4 {
  font-size: 18px;
  font-weight: 700;
  color: white;
  text-transform: capitalize;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-transform: translateX(-20px);
  transform: translateX(-20px);
  opacity: 0;
}

.portfolio .portfolio-item-inner:hover .portfolio-info h4 {
  opacity: 1;
  -webkit-transform: translateX(0px);
  transform: translateX(0px);
}
.portfolio .portfolio-item .portfolio-info .icon {
  height: 40px;
  width: 40px;
  background-color: #ffffff;
  text-align: center;
  border-radius: 50%;
  position: absolute;
  right: 30px;
  bottom: 30px;
  opacity: 0;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-transform: translateX(20px);
  transform: translateX(20px);
}
.portfolio .portfolio-item-inner:hover .portfolio-info .icon {
  opacity: 1;
  -webkit-transform: translateX(0px);
  transform: translateX(0px);
}
.portfolio .portfolio-item .portfolio-info .icon .fa {
  line-height: 40px;
}

/* lightbox */

.lightbox {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(4, 12, 38, 1);
  z-index: 100;
  display: none;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  cursor: zoom-out;
  padding: 30px;
}
.lightbox.open {
  display: -ms-flexbox;
  display: flex;
}
.lightbox .lightbox-content img {
  height: auto;
  width: auto;
  max-width: 100%;
  max-height: 800px;
  cursor: pointer;
  display: block;
  padding: 40px 0 30px;
}
.lightbox .lightbox-content {
  position: relative;
}
.lightbox.open .lightbox-content {
  animation: lightboxImage 0.5s ease;
}
@-webkit-keyframes lightboxImage {
  0% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes lightboxImage {
  0% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.lightbox .lightbox-content {
  position: relative;
}
.lightbox .lightbox-content .lightbox-close {
  position: absolute;
  height: 40px;
  width: 40px;
  top: 0;
  right: 0px;
  font-size: 32px;
  text-align: right;
  color: #ffffff;
  line-height: 40px;
}
.lightbox .lightbox-content .lightbox-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-between;
  justify-content: space-between;
  font-size: 16px;
  color: #ffffff;
  font-weight: 400;
  z-index: -1;
}

.lightbox .lightbox-controls .prev-item,
.lightbox .lightbox-controls .next-item {
  position: absolute;
  height: 40px;
  width: 40px;
  background-color: #ffffff;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
  top: 50%;
  margin-top: -20px;
  z-index: 110;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.lightbox .lightbox-controls .prev-item:hover,
.lightbox .lightbox-controls .next-item:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.lightbox .lightbox-controls .prev-item:active,
.lightbox .lightbox-controls .next-item:active {
  -webkit-transform: scale(1);
  transform: scale(1);
}
.lightbox .lightbox-controls .prev-item {
  left: 30px;
}
.lightbox .lightbox-controls .next-item {
  right: 30px;
}
.lightbox .lightbox-controls .prev-item .fa,
.lightbox .lightbox-controls .next-item .fa {
  font-size: 30px;
  line-height: 40px;
}

@media (max-width: 991px) {
  .portfolio .portfolio-item {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .portfolio .portfolio-filter {
    text-align: left;
    margin-bottom: 50px;
  }
  .portfolio .portfolio-filter button {
    margin-bottom: 5px;
    // transform: translateX(-1500px);
  }
}

@media (max-width: 767px) {
  .portfolio .portfolio-filter {
    flex: 1 0 100%;
    max-width: 100%;
    text-align: left;
    margin-bottom: 50px;
  }
  .portfolio .portfolio-filter button {
    margin-bottom: 5px;
    // transform: translateX(-1500px);
  }

  .avatar {
    // background: black;
    background: rgba(255, 255, 255, 0);
    clip-path: circle(150px at center);
    transition: all 1s ease-in-out;
    img {
      width: 90%;
    }
  }
}

@media (max-width: 575px) {
  .portfolio .portfolio-item {
    flex: 0 0 100%;
    max-width: 100%;
  }
}
