.containerSlider {
  width: 100%;
  height: 100vh;
  display: flex;
  margin: auto;
  //   margin-top: 80px;
  justify-content: center;
  align-items: center;

  .fa {
    display: inline-block;
    font: normal normal normal 18px/1 FontAwesome;
    font-size: 2rem;
    text-rendering: auto;
    color: white;
  }

  // background: yellow;
}

.slide {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 40px;
  flex-shrink: 0;
}

.slide-image {
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  transition: all 0.5;
}

.glide__slide--active .slide-image {
  height: 100%;
}

.slide-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slide p {
  color: white;
  font-size: 14px;
}

// .glide__slide img {
//   width: 100%;
// }

.glide__slide {
  // filter: blur(3px);
  opacity: 0.9;
  transition: 0.3s linear;
}

.glide__slide--active {
  opacity: 1;
  filter: none;
  transform: scale(1.2);
}

.glide__slides {
  overflow: visible;
  color: white;
}

.glide__arrow {
  background-color: rgba(253, 241, 0, 0.5) !important;
  top: 40%;
  border-radius: 0 !important;
}

.glide__arrow:hover {
  background-color: rgba(253, 241, 0, 1) !important;
  .fa {
    color: black;
  }
}

.glide__arrow:active {
  background-color: rgb(232, 0, 253) !important;
}

/* Bottom left text */
.bottom-left {
  position: absolute;
  bottom: 75px;
  left: 140px;
}

/* Top left text */
.top-left {
  position: absolute;
  top: 75px;
  left: 140px;
}

/* Top right text */
.top-right {
  position: absolute;
  top: 75px;
  right: 140px;
}

/* Bottom right text */
.bottom-right {
  position: absolute;
  bottom: 75px;
  right: 140px;
}

/* Centered text */
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

