@media only screen and (max-width: 768px) {
  .containerSlider {
    width: 100%;
    height: 100vh;
    display: flex;
    margin: auto;
    margin-top: 60px;
    justify-content: center;
    align-items: flex-start;
    img {
      object-fit: cover;
      object-position: 50% 0;
      min-height: 600px;
    }

    // background: yellow;
  }

  .glide__arrow {
    background-color: rgba(253, 241, 0, 0.5) !important;
    .fa {
      display: inline-block;
      font: normal normal normal 18px/1 FontAwesome;
      font-size: 1.5rem;
      text-rendering: auto;
      color: white;
    }
  }

  .glide__arrow:hover {
    background-color: rgba(253, 241, 0, 1) !important;
    .fa {
      color: black;
    }
  }

  .glide__arrow:active {
    background-color: rgb(232, 0, 253) !important;
  }

  /* Bottom left text */
  .bottom-left {
    position: absolute;
    bottom: 60px;
    left: 55px;
  }

  /* Top left text */
  .top-left {
    position: absolute;
    top: 60px;
    left: 55px;
  }

  /* Top right text */
  .top-right {
    position: absolute;
    top: 60px;
    right: 55px;
  }

  /* Bottom right text */
  .bottom-right {
    position: absolute;
    bottom: 60px;
    right: 55px;
  }

  /* Centered text */
  .centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
