  color: white;
  position: relative;
}

.iTest {
  img {
    width: 100%;
    height: 100vh;
    position: absolute;
    object-fit: cover;
    right: 0;
    top: 0;
    z-index: 0;
  }
}
.content1 {
  padding: 9em 11%;
  z-index: 1;
  position: absolute;

  h2 {
    font-size: 5em;
    // color: #fff72c;
    margin-bottom: 0;
  }
  p {
    font-size: 1.6em;
  }
}

.wrapper {
