}

.box55 {
  width: 150px;
  height: 150px;
  background-color: yellow;

  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  left: 50%;
  top: calc(50vh - 100px);
  color: white;
  z-index: 66;
}

@media (max-width: 767px) {
  .demo-text .text {
    font-size: 5rem;
    line-height: 1;
    font-weight: 900;
    color: white;
  }

  .bottomtop {
    position: absolute;
    top: -49px;
    right: -10px;
    text-align: center;
  }
  .box55 {
    width: 25px;
    height: 25px;
    background-color: yellow;

    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    top: calc(50vh - 100px);
    color: white;
  }
}
